<script>
import menu from '../mixins/menu';
import ScrollReveal from 'scrollreveal';

export default {
  name: 'Main',
  mixins: [menu],
  methods: {
    animations() {
      this.$reveal('.main');
      this.$revealDistance('.main .text', 'left');
      this.$revealDistance('.main .nav', 'right');
    },
    scrollTo(ref) {
      document.getElementById(ref).scrollIntoView({ behavior: 'smooth' });
    }
  },

  mounted() {
    this.animations();
  }
}
</script>

