      <div class="first-block">
        <svg @click="hide" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 1000 1000" enable-background="new 0 0 1000 1000" xml:space="preserve">
          <g><path d="M945.7,989.4L10,53.8L54.3,9.4L990,945.1L945.7,989.4z"/><path d="M10,946.2L945.7,10.6L990,54.9L54.3,990.6L10,946.2L10,946.2z"/></g>
        </svg>
        <h2>{{ info.name }}</h2>
        <div class="description">
          <h3>Описание</h3>
          <div v-html="info.description">
          </div>
        </div>
      </div>
      <div class="bottom-block">
