      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Product',
  props: ['info'],
  data() {
    return {
      mainImage: this.info.key + '.jpg',
      isActive: true
    }
  },

  methods: {
    first() {
      this.mainImage = this.info.key + '.jpg';
      this.isActive = true;
    },
    second() {
      this.mainImage = this.info.key + '_2.jpg';
      this.isActive = false;
    },
    hide() {
      this.$emit('hide');
      this.$nextTick(() => {
        document.getElementById('products').scrollIntoView();
      });
    }
  },

  mounted() {
    //console.log('main image', this.mainImage);
  }
}
</script>

