<template lang="html">
  <div id="products" class="hb-container shop">
    <div class="products-heading">
      <h2>{{ heading }}</h2>
      <ul class="filters">
        <li v-for='filter in filters'>
          <span @click="selectFilter(filter)" :class="{ 'active' : filter.active }">{{ filter.name }}</span>
        </li>
      </ul>
    </div>
    <transition name="fade">
    <div v-if="!showDetails">
      <div class="grid">
        <div
          v-for="item, index in filteredProducts"
          v-if="index < productsToDisplay && item.visibility === true"
          @click="open(item)"

          class="item">
            <img class="img-first" :src="require('../assets/' + item.key + '.jpg')" alt="">
            <img class="img-second" :src="require('../assets/' + item.key + '_2.jpg')" alt="">
            <div class="overlay">
            </div>
            <span>{{ item.name }}</span>
        </div>
      </div>
      <div class="more">
        <a
          v-if="productsToDisplay < filteredProducts.length"
          @click="productsToDisplay = productsToDisplay + 6;">
          Показать еще
        </a>
      </div>
    </div>
  </transition>
    <transition name="opacity">
    <product
      :info="selectedProduct"
      v-if="showDetails"
      @hide="hideDetails">
    </product>
  </transition>
  </div>
</template>

<script>
import ScrollReveal from 'scrollreveal';
import Product from "./product.vue";
import data from '../mixins/data';

export default {
  name: 'Products',
  components: {
    Product
  },
  mixins: [data],
  data() {
    return {
      showDetails: false,
      productsToDisplay: 6,
      heading: 'Каталог',
      selectedProduct: {}
    }
  },
  computed: {
    filteredProducts() {
      return this.products.filter(el => el.visibility === true);
    }
  },
  methods: {
    open(item) {
      this.selectedProduct = item;
      this.showDetails = true;
      this.$nextTick(() => {
        document.getElementById('product').scrollIntoView();
      });
    },

    hideDetails() {
      this.showDetails = false;
    },

    selectFilter(item) {
      this.filters.forEach(el => {
        el.active = false;
      });
      item.active = true;
      this.products.forEach(el => {
          if (item.tag === 'all' || el.tag === item.tag) el.visibility = true;
          else el.visibility = false;
        });
      this.productsToDisplay = 6;
      this.showDetails = false;
    },

    animations() {
      this.$reveal('.products-heading h2');
      this.$reveal('.products-heading .filters');
      this.$reveal('.shop .grid .item');
      this.$reveal('.shop .more');
    }
  },

  mounted() {
    this.animations();
  }
}
</script>

