  margin-bottom: $xxl;

  li {
    @include regular2;
    padding: 0 $m;
  }

  li::after {
    content: '/';
    position: relative;
    left: $m
  }

  li:last-child::after {
    display: none
  }

  span.active {
  /*  color: #fff;
    background-color: $link; */
    color: $font1;
    border-bottom: 1px solid $font1
  }

  span:not(.active):hover {
    cursor: pointer
  }

  /* Animation */

  $transition: 0.3s ease-in-out;

  span {
