import Vue from 'vue';
import Vuex from 'vuex';
import App from './App.vue';
import ScrollReveal from 'scrollreveal';
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css';

Vue.use(Vuex);
Vue.use(BootstrapVue);
Vue.use(IconsPlugin);

Vue.config.productionTip = false;

const store = new Vuex.Store({
  state: {
    animation: {
      duration: 1000,
      viewFactor: 0.3,
      opacity: 0,
      easing: 'ease-in-out'
    }
  }
});

ScrollReveal().debug = true;

Vue.prototype.$reveal = item => {
  ScrollReveal().reveal(item, store.state.animation);
  console.log('store', ScrollReveal().store);
};

Vue.prototype.$revealDistance = (item, origin) => {
  ScrollReveal().reveal(item, {
    duration: 1000,
    viewFactor: 0.3,
    distance: '50px',
    origin: origin,
    opacity: 0,
    easing: 'ease-in-out'
  });
  console.log('store', ScrollReveal().store);
};

Vue.prototype.$sync = () => {
  ScrollReveal().sync();
};

new Vue({
  store,
  render: h => h(App),
}).$mount('#app');
